import React, { useCallback, useState } from 'react'
import { useApproveBoardroom } from 'hooks/useApprove'
import styled from 'styled-components'
import DepositModal from 'views/Farms/components/DepositModal'
import WithdrawModal from 'views/Farms/components/WithdrawModal'
import { AddIcon, Button, Card, CardBody, IconButton, MinusIcon, useModal } from 'uikit'
import Value from 'components/Value'
import Label from 'components/Label'
import { QuoteToken } from 'config/constants/types'
import useTokenBalance from 'hooks/useTokenBalance'
import { useStakeToBoardroom, useWithdrawFromBoardroom } from 'hooks/useStake'

const IconButtonWrapper = styled.div`
  display: flex;
  svg {
    width: 20px;
  }
`

const Stake: React.FC<any> = ({ canWithdraw, balanceOf }) => {
  const [requestedApproval, setRequestedApproval] = useState(false)
  const { onApprove, isApproved } = useApproveBoardroom()
  const tokenBalance = useTokenBalance(QuoteToken.SSO)
  const { onStake } = useStakeToBoardroom()
  const { onWithdraw } = useWithdrawFromBoardroom()

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const [onPresentDeposit] = useModal(<DepositModal max={tokenBalance} onConfirm={onStake} tokenName="SSO" />)
  const [onPresentWithdraw] = useModal(<WithdrawModal max={balanceOf} onConfirm={onWithdraw} tokenName="SSO" />)

  return (
    <Card>
      <CardBody>
        <StyledCardContentInner>
          <StyledCardHeader>
            <Value value={balanceOf?.toFixed(4)} />
            <Label text="SSO Staked" />
          </StyledCardHeader>
          <StyledCardActions>
            {!isApproved ? (
              <Button disabled={requestedApproval} onClick={handleApprove}>
                Approve SSO
              </Button>
            ) : (
              <IconButtonWrapper>
                <IconButton variant="tertiary" onClick={onPresentWithdraw} mr="6px" disabled={!canWithdraw}>
                  <MinusIcon color="primary" />
                </IconButton>
                <IconButton variant="tertiary" onClick={onPresentDeposit}>
                  <AddIcon color="primary" />
                </IconButton>
              </IconButtonWrapper>
            )}
          </StyledCardActions>
        </StyledCardContentInner>
      </CardBody>
    </Card>
  )
}

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`

export default Stake
